import React, { useState, useEffect } from "react";
import "./NavbarMenu.css";
import NavbarItem from "./NavbarItem";
function Navbar() {
  const [activeNavbarItem, setActiveNavbarItem] = useState("home");
  const [scrolled, setScrolled] = useState(false);

  // xu ly cun
  useEffect(() => {
    const onHandleScroll = () => {
      // so pixel  cun theo chieu dc > 50 , cun qu 50px setSrolled else gn u trang < 50px th thi
      if (window.scrollY > 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };

    // cpn mount (hin th first) ,gn sk scroll vo window
    window.addEventListener("scroll", onHandleScroll);

    // clean func chy khi cpn unmount ||trc khi use effect chy li
    // g sk scoll trc , trnh trng
    return () => window.removeEventListener("scroll", onHandleScroll);
  }, []);

  function onUpdateActiveNavbarItem(value) {
    return setActiveNavbarItem(value);
  }
  return (
    <div className="navbar">
      <nav className="navbar-nav">
        <NavbarItem
          content="Home"
          goToId="#home"
          className={activeNavbarItem === "home" ? "active" : ""}
          onClick={() => onUpdateActiveNavbarItem("home")}
        />
        <NavbarItem
          content="About Me"
          goToId="#about_me"
          className={activeNavbarItem === "about_me" ? "active" : ""}
          onClick={() => onUpdateActiveNavbarItem("about_me")}
        />
        <NavbarItem
          content="Skills"
          goToId="#skill"
          className={activeNavbarItem === "skill" ? "active" : ""}
          onClick={() => onUpdateActiveNavbarItem("skill")}
        />
        <NavbarItem
          content="Projects"
          goToId="#project"
          className={activeNavbarItem === "project" ? "active" : ""}
          onClick={() => onUpdateActiveNavbarItem("project")}
        />
      </nav>
    </div>
  );
}
export default Navbar;
