import "./Detail.css";
import { useParams } from "react-router-dom";
import Project from "../../layout/DefaultLayout/Project/Project";
import images from "../../assets/images/images";
import StarsEffect from "../../components/StartsEffect/StarsEffect";
function Detail() {
  const { project } = useParams(); // weathery | rebox | caro
  let src = null;
  let detail = null;
  let deploy = null;
  let local = null;
  let nameProject = null;
  let description = null;
  let role = null;
  let tech = null;
  let feature = null;
  let git = null;
  if (project === "weathery") {
    src = images.weathery;
    deploy = true;
    detail = false;

    nameProject = "Website D bo thi tit - Weathery";
    description =
      "y l mt d n c nhn hin th thng tin thi tit hin ti, chi tit v tr v cc ch s thi tit bng d liu ly t Weather API. Trang web cng cung cp d bo thi tit theo gi trong ngy v d bo 7 ngy, bao gm nhit , lng ma v hng gi. D n tp trung ch yu vo pht trin giao din ngi dng (frontend) v thit k UI.";
    role = "Frontend Developer";
    tech = "ReactJS";
    feature = `---- Tm kim quc gia v hin th thi tit ca quc gia .
        ---- Hin th thi tit hin ti cng thng tin v tr chi tit v cc ch s thi tit ( m, ch s UV, ).
        ---- D bo thi tit theo gi trong ngy hin ti.
        ---- D bo 7 ngy vi nhit , lng ma v hng gi.
    `;
    git = "https://github.com/Nhungnguyen0804/weathery";
  } else if (project === "rebox") {
    src = images.rebox;
    deploy = true;
    detail = false;
    nameProject = "Website Trao i, thanh l, mua bn  c - ReBox";
    description =
      "Rebox l mt website cho php hc sinh trung hc ng tin  bn, trao i hoc quyn gp cc vt dng  qua s dng. H thng h tr ng tin, tham gia s kin, hin th thng k v kim sot vic hin th thng tin lin lc ca ngi ng.";
    role = "Fullstack Developer";
    tech = "Flask (Python) + HTML,CSS,JS + SQLite";

    feature = `Nhim v ca ti bao gm:
    ---- Xy dng cc chc nng ng k, ng nhp, qun mt khu, gi OTP qua email, to bo co thng k (ngi dng, bi ng) v qun l ti khon c nhn (xem, chnh sa, xa h s).
    ---- To cc tnh nng xem trc bi ng, hin th bi ng theo danh mc, lc theo loi v ngy to, cng nh n/hin thng tin lin h.
    ---- Pht trin giao din trang ch v xy dng phn front-end cho cc chc nng c trin khai  back-end.
    ---- Phn tch v thit k h thng: xy dng s  DFD cp 0, cp 1 v cp 2.`;
    git = "https://github.com/Nhungnguyen0804/PTTKHT_Project";
  } else if (project === "caro") {
    src = images.caro;
    local = true;
    detail = false;
    nameProject = "Tr chi Caro - CaroGame";
    description =
      "ng dng game c Caro cho php thi u gia ngi chi vi ngi chi hoc ngi chi vi my, s dng thut ton minimax  tnh ton nc i ti u cho my";
    role = "Fullstack Developer";
    tech = "Python+PyGame";
    feature = `---- Lp CaroGame chu trch nhim qun l v iu khin tr chi(logic ca nc i, kim tra iu kin thng, undo)
                ---- Cc hm tnh ton im cho cc nc i , hm minimax tm nc i ti u cho my , ch  chi c Caro gia ngi vi ngi hoc ngi vi my`;
    git = "https://github.com/Nhungnguyen0804/CaroGame";
  } else return <div>Cha c thng tin chi tit!</div>;
  return (
    <div className="detail ">
      <StarsEffect />
      <div className="detail-left">
        <Project
          src={src}
          projectName="Weathery - ReactJs Project"
          detail={detail}
          deploy={deploy}
          local={local}
          target="_blank"
          external="https://nhungnguyen0804.github.io/weathery/"
          internal={"/detail/weathery"}
        />
      </div>
      <div className="detail-right">
        <div className="general-title">Detail Project</div>
        <div className="detailRight-content">
          <h2 className="general-h2">Tn d n: {nameProject}</h2>
          <div className="detail-marginBottom">
             M t:
            <br /> {description}
          </div>
          <div className="detail-marginBottom"> Vai tr: {role}</div>
          <div className="detail-marginBottom">
             Cng ngh s dng: {tech}
          </div>
          <div
            className="detail-marginBottom"
            style={{ whiteSpace: "pre-line" }}
          >
             Chc nng:
            <br />
            {feature}
          </div>

          <div className="detail-marginBottom"> Source Code: {git}</div>
        </div>
      </div>
    </div>
  );
}
export default Detail;
